import React from 'react'
import PropTypes from 'prop-types'
import { Link, graphql } from 'gatsby'
import styled from 'styled-components'

import { Layout, Article, Wrapper, Button, SectionTitle } from '../components'

const Content = styled.div`
  grid-column: 2;
  box-shadow: 0 4px 120px rgba(0, 0, 0, 0.1);
  border-radius: 1rem;
  padding: 3rem 6rem;
  @media (max-width: ${props => props.theme.breakpoints.tablet}) {
    padding: 3rem 2rem;
  }
  @media (max-width: ${props => props.theme.breakpoints.phone}) {
    padding: 2rem 1.5rem;
  }
  overflow: hidden;
`

const Hero = styled.div`
  grid-column: 2;
  padding: 3rem 2rem 6rem 2rem;
  text-shadow: 0 12px 30px rgba(0, 0, 0, 0.15);
  color: ${props => props.theme.colors.grey.dark};

  @media (max-width: ${props => props.theme.breakpoints.phone}) {
    padding: 2rem 1rem 4rem 1rem;
  }

  p {
    font-size: 1.68rem;
    margin-top: -1rem;
    @media (max-width: ${props => props.theme.breakpoints.tablet}) {
      font-size: 1.45rem;
    }
    @media (max-width: ${props => props.theme.breakpoints.phone}) {
      font-size: 1.25rem;
    }
  }
`
const SocialDiv = styled.div`
  margin: 20px 0 20px 0;
`
const SocialIcon = styled.a`
  padding: 0 3px 0 0;
`

const IndexPage = ({
  data: {
    allMdx: { nodes: posts },
  },
}) => (
  <Layout>
    <Wrapper>
      <Hero>
        <h1>Hola hola!</h1>
        <p>
          Soy Fernando Valdes, desarrollador java y node desde hace algunos años. Hoy me contratan como arquitecto de soluciones.
        </p>
        <br/>
        <p>
          Acá vas a poder encontrar artículos que escribí sobre desarrollo personal y profesional orientado a la construcción de aplicaciones y servicios.
        </p>
        <SocialDiv>
          <SocialIcon>
            <a href="https://www.linkedin.com/in/fervaldes/">
              <img src="social/linkedin.svg" alt="Linkedin" width="45px" height="45px" />
            </a>
          </SocialIcon>
          <SocialIcon>
            <a href="https://github.com/ktufernando">
              <img src="social/github.svg" alt="Github" width="45px" height="45px" />
            </a>
          </SocialIcon>
          <SocialIcon>
            <a href="https://www.instagram.com/ktufernando/">
              <img src="social/instagram.svg" alt="Instagram" width="45px" height="45px" />
            </a>
          </SocialIcon>
          <SocialIcon>
            <a href="https://medium.com/@ktufernando/">
              <img src="social/medium.svg" alt="Medium" width="45px" height="45px" />
            </a>
          </SocialIcon>
        </SocialDiv>
        <Link to="/contact">
          <Button big>
            <svg width="1792" height="1792" viewBox="0 0 1792 1792" xmlns="http://www.w3.org/2000/svg">
              <path d="M1764 11q33 24 27 64l-256 1536q-5 29-32 45-14 8-31 8-11 0-24-5l-453-185-242 295q-18 23-49 23-13 0-22-4-19-7-30.5-23.5t-11.5-36.5v-349l864-1059-1069 925-395-162q-37-14-40-55-2-40 32-59l1664-960q15-9 32-9 20 0 36 11z" />
            </svg>
            Mandame un mensaje!
          </Button>
        </Link>
      </Hero>
      <Content>
        <SectionTitle>Últimas historias</SectionTitle>
        {posts.map(post => (
          <Article
            title={post.frontmatter.title}
            date={post.frontmatter.date}
            excerpt={post.excerpt}
            timeToRead={post.timeToRead}
            slug={post.fields.slug}
            categories={post.frontmatter.categories}
            key={post.fields.slug}
          />
        ))}
      </Content>
    </Wrapper>
  </Layout>
)

export default IndexPage

IndexPage.propTypes = {
  data: PropTypes.shape({
    allMdx: PropTypes.shape({
      nodes: PropTypes.array.isRequired,
    }),
  }).isRequired,
}

export const IndexQuery = graphql`
  query IndexQuery {
    allMdx(sort: { fields: [frontmatter___date], order: DESC }) {
      nodes {
        fields {
          slug
        }
        frontmatter {
          title
          date(formatString: "MM/DD/YYYY")
          categories
        }
        excerpt(pruneLength: 200)
        timeToRead
      }
    }
  }
`
